.about_top_section {
  position: relative;
  background-image: url("../../images/p6.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 30rem;
  width: 100%;
}
.about_top_heading {
  position: relative;
}
.about_top_heading h1 {
  color: #fff;
  text-align: center;
  padding: 8rem 0 0 1rem;
  opacity: 1;
}
.navlink_homeandcontact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.home_and_about p {
  display: inline-block;
  text-align: center;
  color: #fff !important;
}
.home_and_about p:hover {
  color: #fff !important;
  font-size: 1.1rem;
}
.digial_future {
  margin-top: 5rem;
}
.digial_future h4 {
  color: #c2c2c2;
  font-size: 1rem;
  padding: 1rem 0 0 0;
}
.digial_future h3 {
  color: #ffffff;
  font-size: 1.8rem;
}
.digial_future p {
  color: #ffffff;
  font-size: 0.8rem;
  padding-top: 2rem;
}

/* seconde part csss */

.stort_heading,
.vision_heading {
  position: relative;
  margin-top: 8rem;
}
.stort_heading h2 {
  color: #144d79;
  border-bottom: 4px solid #ee6060;
  width: 75%;
}
.stort_heading p,
.vision_heading p {
  color: #707070;
  padding: 2rem 0 2rem 0;
}
.btn_explore_more {
  font-size: 0.8rem;
  padding: 10px 25px;
  background: #144d79 0% 0% no-repeat padding-box;
  box-shadow: 10px 10px 50px #0000001a;
  border-radius: 100px;
  color: #fff;
  outline: none !important;
  border: none;
  opacity: 1;
}

.story_video iframe{
  width:100%;
  height:20rem;
  margin-top: 5rem;
}
/* third section css */
.vision_heading h2 {
  color: #144d79;
  border-bottom: 4px solid #ee6060;
  width: 33%;
}
.header_top_img1 {
  margin-top: 5rem;
  width: 100%;
  height: 80%;
}
/* values section css */
.value_about_heading h2 {
    color: #144d79;
    border-bottom: 4px solid #ee6060;
    width: 28%;
  }
  .value_about_heading p {
    color: #707070;
    padding: 2rem 0 2rem 0;
  }
  .respect_box1{
    display: flex;
    justify-content:space-around;
    align-items: center;
    flex-direction: row;
    margin-top: 1rem;
    width:80%;
   max-height:60%;
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 0px 3px 15px #00000029;
border-radius: 34px;
opacity: 1;
}
.repect_onecol{
  display: flex;
  align-items: center;
  /* justify-content: center; */
  flex-direction: column;
}
.first_img img{
    display: inline-block;
    width: 3rem;
    height: 3rem;
   
}
.first_img p{
    display: inline-block;
    font-size: 1rem;
    color: #333;
    padding-left: .5rem;
}
.btn_round{
    margin-top: 1rem;
    width: 3rem;
    height: 3rem;
    border-radius: 50% !important;
   color:#fff;
   border: 2px solid #fff !important;
    background: #ec615b !important;
    -webkit-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -o-transition: 0.5s all;
    -ms-transition: 0.5s all;
    transition: 0.5s all;
}
.card_heading p{
    color: #333;
    font-size: 1.2rem;
}
.myperafirst{
    color: #333;
    font-size: 1.2rem;
}
/* what we get section */
.we_get_section{
    margin-top: 3rem;
}
.get_heading h2 {
    color: #144d79;
    border-bottom: 4px solid #ee6060;
    width: 28%;
  }
  .get_heading p {
    color: #707070;
    padding: 2rem 0 2rem 0;
  }
  .benefit_heading h3{
    color: #2C2024;
    font-size: 1.5rem;
    text-align: center;
  }
  .benefit_heading p{
    color: #707070;
    padding: .5rem 0;

 
  }
  .gift_and_heart_img{
    width: 3rem;
    height: 3rem;
  }
  /* our people css start here */
  .our_people_section{
    margin-top: 3rem;
}
.our_people_heading h2 {
    color: #144d79;
    border-bottom: 4px solid #ee6060;
    width: 28%;
  }
  .our_people_heading p {
    color: #707070;
    padding: 2rem 0 2rem 0;
  }
  .people_box{
      width:100%;
      height: 16rem;
      background-image: url('../../images/cover.svg');
      background-size: 100% 100%;
      background-repeat: no-repeat;
    border-radius: 6px;
    opacity: 1;
  }
  .people_box h3{
      padding-top: 12rem;
      font-size: 1.3rem;
      color: #fff;
      text-align: center;
   
}
.people_box h4{
    font-size: .8rem;
    color: #fff;
    text-align: center;
 
}
