.navbar {
    /* background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%); */
    background: #FFFFFF 0% 0% no-repeat padding-box;
box-shadow: 10px 10px 35px #0000000D;
opacity: 1;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  .menu_active {
    font-size: bold;
    border-bottom: 2px solid #565387 !important;
  }
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #147944;
    justify-self: start;
    margin-left: 30px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    font-family: 'Roboto', sans-serif;
  }
  .logo_img{
    width: 8rem;
    height: 3.5rem;
  }
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(7, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width:80vw;
    justify-content:end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 40px;
    text-decoration: none;
  }
  
  .nav-links {
    color: #212121 !important;
    display: flex;
    align-items: center;
    align-items:space-between;
    text-decoration: none !important;
    padding:0.5rem 1rem;
    height: 100%;
    font-size: 1rem !important;
  }
  .dropdown_color{
    color: #212121 !important;
    display: flex;
    align-items: center ;
    align-items:space-between;
    text-decoration: none !important;
    padding:0.5rem 1rem ;
    height: 100%;
    font-size: 1rem !important;
  }
  .nav-links:hover {
    color: #207181 !important;
    border-bottom: 2px solid #207181 !important;
    transition: all 0.2s ease-out;
  }
  .dropdown_color:hover{
    color: #207181 !important;
    border-bottom: 2px solid #207181 !important;
    transition: all 0.2s ease-out;
  }
  .fa-bars {
    color: #fff !important;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .navbar {
     background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
   
      }
