      position: relative;
    }
    .logo_img{
      width: 7rem;
      height: 3rem;
    }
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 1rem !important;
      width: 100%;
      display: table;
      color:#fff !important;
      font-size: 1rem !important;

    }
  .dropdown_color{
    text-align: center !important;
    padding-bottom: 0rem !important;
    width: 100%;
    display: table;
    color:#fff !important;
    border: none !important;
    font-size: 1rem !important;
    background-color:none !important;
  }
  .dropdown_color:hover {
    color: #207181 !important;
    border-bottom:none !important;
    border: none !important;
    transition: all 0.2s ease-out;
    background-color:none !important;
}
    .nav-links:hover {
      background-color: #fff;
      color: #242424 !important;
      border-radius: 0;
    }
    .navbar-logo {
      margin-left: 10px;
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
