.blog_top_section {
    position: relative;
    background-image: url("../../images/Blog.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 25rem;
    width: 100%;
  }
  .blog_top_heading {
    position: relative;
  }
  .blog_top_heading h1 {
    color: #2C2024;
    text-align: center;
    padding: 8rem 0 0 1rem;
    opacity: 1;
  }
  .navlink_homeandcontact {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .home_and_blog p {
    display: inline-block;
    text-align: center;
    color: #2C2024;
  }
  .home_and_blog p:hover {
    color: #2C2024;
    font-size: 1.1rem;
  }
  
  /* topics section css here */
  .topics_heading{
      position: relative;
  }
  .topics_heading h1{
      text-align: center;
      margin: 4rem 0 1.5rem 0;
    color: #144D79;
    text-shadow: 0px 3px 6px #00000029;
    opacity: 1;
      font-size: 2rem;
      

  }
  .line2{
      position: absolute;
      top: 35%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: #EE6060 0% 0% no-repeat padding-box;
      border: 2px solid #EE6060;
      width: 52%;
  }
  .topics_heading h3{
      padding-top: 7rem;
      text-align: center;
    font-size: 1.3rem;
    color: #2C2024;

}

/* blogvideo section css */
.blog_video_section{
    margin-top: 2rem;
}
.blog_video_box{
    width: 18rem;
    height: 21rem;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 24px #0000003D;
  border-radius: 4px;
  opacity: 1; 
}
.blog_video_box iframe{
    width: 18rem;
    height: 9rem;
}
.blog_video_box h5{
  font-size: 1rem;
  color: #2C2024;
  padding: 1rem;
}
.blog_video_box p{
    font-size: .6rem;
    color: #707070;
    padding-left: 1rem;
  }
.read_more{
    background-color: none;
    border: none;
    color: #ED1A3B;
    text-transform: uppercase;
    opacity: 1;
padding-left: 1rem;
font-size: .8rem;
}
@media screen and (max-width:800px){
  .blog_top_section {
    height: 15rem;
    width: 90%;
  }
  .blog_top_heading h1 {
    color: #2C2024;
    text-align: center;
    padding: 4rem 0 0 1rem;
    opacity: 1;
  }
   /* topics section css here */

.topics_heading h1{
    margin: 2rem 0 0rem 0;
    font-size: 1.1rem;
}
.line2{
  top: 35%;
  left: 50%;
  width: 60%;
}
.topics_heading h3{
  padding-top: 3rem;
  text-align: center;
font-size: 1rem;
}
/* blogvideo section css */
.blog_video_section{
  margin-top: 2rem;
}
.blog_video_box{
  width:100%;
  height: 21rem;
}
.blog_video_box iframe{
  width: 100%;
  
}
}